@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@700&display=swap);
*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.header{
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    background-color: #082032;
    padding: 2rem 2rem 1rem 2rem;
    font-family: 'Varela Round', sans-serif;
    font-size: 1rem;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
}

.header>div>h1{
    color: rgb(170, 168, 165);
    text-transform: uppercase;
}

.header>.buttons{
    width: 100%;
    display: flex;
    justify-content: space-around;
    margin-top: 1rem;
}

.header>.buttons>button{
    padding: 0.7rem;
    font-family: 'Varela Round', sans-serif;
    border-radius: 20px;
    border: none;
    color: #fff;
    background-color:#082032;
    transition: all 0.5s ease-in;
}

.header>.buttons>button:hover{
    box-shadow: 10px 10px 30px 0px rgb(170, 168, 165);
    -webkit-box-shadow: 10px 10px 30px 0px rgb(170, 168, 165);
    -moz-box-shadow: 10px 10px 30px 0px rgb(170, 168, 165);
}

.header>.buttons>.all:hover{
    font-weight: bolder;
}

.header>.buttons>.comp:hover{
    color: green;
    font-weight: bolder;
}

.header>.buttons>.pen:hover{
    color: #FF0000;
    font-weight: bolder;
}


.cardsContainer{
    display: flex;
    flex-wrap: wrap;
    margin: 2rem;
    justify-content: center;
}

.cardsContainer>.cards{
    display:flex;
    flex-direction: column;
    text-align: center;
    width: 16%;
    box-shadow: 15px 15px 9px -8px rgba(0,0,0,0.75);
    -webkit-box-shadow: 15px 15px 9px -8px rgba(0,0,0,0.75);
    -moz-box-shadow: 15px 15px 9px -8px rgba(0,0,0,0.75);
    padding: 1rem;
    margin: 0.1rem;
    margin: 10px;
    border-radius: 0.5rem;
}
.cardsContainer>.cards>.cardButton{
    padding-top: 20px;
}

.cardsContainer>.cards>.cardButton>button{
    margin-bottom: 8px;
    padding: 1rem;
    border-radius: 1rem;
    text-transform: uppercase;
    font-family: 'Oswald', sans-serif;
    border: none;
    color: #fff;
    letter-spacing: 0.3rem;
}

.cardsContainer>.cards>.cardButton>.completed{
    background-color: green;
}

.cardsContainer>.cards>.cardButton>.unCompleted{
    background-color: red;
}

@media only screen and (max-width: 600px) {

    .cardsContainer{
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    .cardsContainer>.cards{
      width:20rem;
      margin: 1rem;
      height: 10.5rem;
      font-size: 10px;
      border-radius: 20px;
      box-shadow: 15px -7px 14px -5px rgba(0,0,0,0.75);
      -webkit-box-shadow: 15px -7px 14px -5px rgba(0,0,0,0.75);
      -moz-box-shadow: 15px -7px 14px -5px rgba(0,0,0,0.75);
    }
  
    .cards>.card-text>p{
      margin-top: 5px;
    }
  
    .cards>.cardButton{
      margin-top: 10px;
      margin-bottom: 10px;
    }
  
}
*{
    box-sizing: border-box;
}

.loader-container{
    position: relative;
    width: 60px;
    height: 60px;
    margin: 10px;
}

.loader-container>.loaderOne{
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    border: solid 4px transparent;
    border-top-color: rgb(27, 255, 27);
    border-left-color: rgb(27, 255, 27);
    border-radius: 50%;
    -webkit-animation: loaderOne 1.4s linear infinite;
            animation: loaderOne 1.4s linear infinite;
}
.loader-container>.loaderTwo{
    position: absolute;
    top: 50%;
    left: 50%;
    width: 70%;
    height: 70%;
    border: solid 4px transparent;
    border-top-color: rgb(3, 59, 240);
    border-left-color: rgb(3, 59, 240);
    border-radius: 50%;
    -webkit-animation: loaderTwo 1.2s linear infinite;
            animation: loaderTwo 1.2s linear infinite;
}

@-webkit-keyframes loaderOne {
    0%{
        -webkit-transform: translate(-50%, -50%) rotate(0deg);
                transform: translate(-50%, -50%) rotate(0deg);
    }
    100%{
        -webkit-transform: translate(-50%, -50%) rotate(360deg);
                transform: translate(-50%, -50%) rotate(360deg);
    }
}

@keyframes loaderOne {
    0%{
        -webkit-transform: translate(-50%, -50%) rotate(0deg);
                transform: translate(-50%, -50%) rotate(0deg);
    }
    100%{
        -webkit-transform: translate(-50%, -50%) rotate(360deg);
                transform: translate(-50%, -50%) rotate(360deg);
    }
}

@-webkit-keyframes loaderTwo {
    0%{
        -webkit-transform: translate(-50%, -50%) rotate(0deg);
                transform: translate(-50%, -50%) rotate(0deg);
    }
    100%{
        -webkit-transform: translate(-50%, -50%) rotate(-360deg);
                transform: translate(-50%, -50%) rotate(-360deg);
    }
}

@keyframes loaderTwo {
    0%{
        -webkit-transform: translate(-50%, -50%) rotate(0deg);
                transform: translate(-50%, -50%) rotate(0deg);
    }
    100%{
        -webkit-transform: translate(-50%, -50%) rotate(-360deg);
                transform: translate(-50%, -50%) rotate(-360deg);
    }
}
